












































































































































































::-webkit-scrollbar{
  width: 10px;
  height: 10px;
  background-color: black;
}
/*滚动条的轨道*/
::-webkit-scrollbar-track{
  /*box-shadow: inset 0 0 5px black;*/
  background-color: white;
}
/*滚动条的滑块按钮*/
::-webkit-scrollbar-thumb{
  border-radius: 10px;
  background-color: white;
  box-shadow: inset 0 0 5px #000;
}
/*滚动条的上下两端的按钮*/
::-webkit-scrollbar-button{
  height: 10px;
  background-color: white;
}



